.friend-list {
    width: fit-content;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    
}
.item{
    min-width: 200px;
    display: flex;
    flex-direction: row;
    gap: 10px;
    align-items: center;
    
    color: #122236;
    padding: 10px 20px;
    background-color: #FFFFFF;
    border-radius: 4px;
    box-shadow: 0 0 10px 10px #61697210;
}